// https://maps.googleapis.com/maps/api/distancematrix/json?origins=Seattle&destinations=San+Francisco&key="AIzaSyDkmSzXgb9Uc7BMkDZRe3e9gtCGJUAdeSM"
function evaluate1(){

    var places = new google.maps.places.Autocomplete(document.getElementById('src'));
    google.maps.event.addListener(places, 'place_changed', function () {
        var place = places.getPlace();
        // var src_latitude = place.geometry.location.A;
        // var src_longitude = place.geometry.location.F;
       
    });
}    
function evaluate2(){
    var places = new google.maps.places.Autocomplete(document.getElementById('dest'));
    google.maps.event.addListener(places, 'place_changed', function () {
        var place = places.getPlace();

        src = document.getElementById('src').value;
    dest = document.getElementById('dest').value; 
    var src1 = src;
    var dest1 = dest;  
    console.log(src)
    console.log(dest)
    src = "Source : "+src
    dest ="<br>"+"Destination : "+dest
    var cost,duration,length;
    document.getElementById('src1').innerHTML = src;
    document.getElementById('dest1').innerHTML = dest;
    var service = new google.maps.DistanceMatrixService();
    service.getDistanceMatrix({
        origins: [src1],
        destinations: [dest1],
        travelMode: 'DRIVING',
        avoidHighways: false,
        avoidTolls: false
      }, function(response, status) {
        if (status !== 'OK') {
          alert('Error was: ' + status);
        } else {
            console.log(response)
            length = response.rows[0].elements[0].distance.text;
            duration = response.rows[0].elements[0].duration.text;
            console.log(length)
            console.log(duration)
            cost = parseFloat(length) * 5;
            console.log(cost)
            length = "<br>"+"Distance : " + length 
            duration = "<br>"+"Duration : " + duration 
            cost = "<br>"+"Cost : " + cost 
            document.getElementById('length1').innerHTML = length
            document.getElementById('duration1').innerHTML= duration
            document.getElementById('cost1').innerHTML= cost

        }
    });
       

        // var dest_latitude = place.geometry.location.A;
        // var dest_longitude = place.geometry.location.F;
       
    });
} 
