class Ticket extends Component {
    constructor(props) {
        super(props);
        this.state = {source:"",destination:"",distance:""};
      }
      
  render() {
    return (
        <div>
        <div className = "row bus-card">
        <div>   
        <Row>
            <Input l={6} label="Source" id="src" onChange={evaluate1}></Input>
        </Row> 
        
        <Row>
            <Input l={6} label="Destination" id="dest" onChange={evaluate2}></Input>
        </Row>
        <Modal 
            header='Bus Ticket'
            trigger={<Button waves='light' className="button_cal">Calculate</Button>}>
            <div id="src1"></div>
            <div id="dest1"></div>
            <div id="length1" ></div>
            <div id="duration1" ></div>
            <div id="cost1" ></div><br/>
            <Button>Pay</Button>
        </Modal>
        </div>
        </div>
       </div> 
    );
  }
}

export default Ticket;



