var bustop = require('./bustop.json');
var source1,destination1;

function avaialBus(){
  console.log(source1)
  console.log(destination1)
   var array1=bustop[source1];
   var array2=bustop[destination1];
   var result = array1.filter(function(n) {
    return array2.indexOf(n) !== -1;
    });
    var printThis="" 
    if(result!==undefined){
    for(var i=0;i<result.length;i++){
          printThis += "<br>"+result[i];
          }
        }
        else{
          printThis="NO BUS FOUND";
        }
        document.getElementById('bus_list').innerHTML=printThis;
}

